@media (max-width: 1024px) {
    /*Header*/
    .sectionPromo__banner {
        height: 260px;
        background-size: 100%;
    }

    .sectionPromo__banner .container {
        padding-top: 1.3%;
    }

    .sectionPromo__banner-wrapper {
        width: 70%;
    }

    .sectionPromo__banner_btn {
        width: 40%;
    }

    /*Home page*/
    .sectionProductType__item {
        justify-content: space-between;
    }

    .sectionProductType__btn {
        padding: 10px;
    }

    .sectionSelection__items {
        flex-wrap: wrap;
    }

    .sectionSelection__item {
        width: calc(32.6% - 20px);
    }

    .sectionUsDifferent__wrapper {
        width: 30%;
    }

    /*Shop page*/
    .accordion {
        width: 24%;
    }

    .product__item {
        width: calc(32.6% - 20px);
    }

    /*Product page*/
    .product__image {
        height: max-content;
    }

    /*Subscriptions page*/
    .subscriptions__sectionPromoBanner {
        height: 260px;
        background-size: 100%;
    }

    .subscriptions__sectionPromoBanner .container {
        padding-top: 3%;
    }

    .subscriptions__sectionPromoBanner_title {
        font-size: 36px;
    }

    .subscriptions__sectionPromoBanner_content {
        font-size: 18px;
    }

    .subscriptions__sectionPromoBanner_btn {
        font-size: 16px;
        width: 50%;
    }

    .subscriptions__sectionSelection_items {
        width: 90%;
    }

    .subscriptions__sectionGiveSubscription {
        height: 260px;
        background-size: 100%;
    }

    .subscriptions__sectionGiveSubscription_wrapper {
        width: 30%;
        margin-top: 2%;
    }

    /*Footer*/
    .footer__bot_list-wrapper {
        width: 20%;
    }
}

@media (max-width: 768px) {
    /*Header*/
    .sectionPromo__banner {
        height: 200px;
    }

    .sectionPromo__banner-wrapper {
        width: 65%;
    }

    .sectionPromo__banner_title {
        font-size: 24px;
    }

    .sectionPromo__banner_content {
        font-size: 16px;
        color: #a7a1a1;
    }

    /*Home page*/
    .sectionProductType > .container {
        flex-wrap: wrap;
        justify-content: space-around;
        gap: 2%;
    }

    .sectionProductType__item {
        width: 48%;
        gap: 10px;

        margin-top: 3%;
    }

    .sectionUsDifferent__wrapper {
        width: 40%;
    }

    /*Shop page*/
    .product__item {
        width: calc(49.5% - 20px);
    }

    .accordion {
        width: 35%;
    }

    /*Subscriptions page*/
    .subscriptions__sectionPromoBanner {
        height: 200px;
    }

    .subscriptions__sectionPromoBanner .container {
        padding-top: 1%;
    }

    .subscriptions__sectionPromoBanner_wrapper {
        width: 60%;
    }

    .subscriptions__sectionPromoBanner_title {
        font-size: 24px;
    }

    .subscriptions__sectionPromoBanner_btn {
        width: 60%;
    }

    .subscriptions__sectionAdvantages_wrapper {
        flex-wrap: wrap;
        gap: 2%;
        justify-content: center;
    }

    .subscriptions__sectionAdvantages_item {
        margin-top: 5%;
    }

    .subscriptions__sectionAdvantages_item-title {
        font-size: 20px;
        font-weight: 700;
    }

    .subscriptions__sectionGiveSubscription {
        height: 200px;
        align-items: start;
    }

    .subscriptions__sectionGiveSubscription_wrapper {
        width: 50%;
        margin-top: 2%;

        background-color: transparent;
        color: #FFFFFF;
        gap: 5px;
        padding: 0;
    }

    .subscriptions__sectionGiveSubscription_btn {
        width: 30%;
    }


    /*Footer*/
    .footer__top .container {
        width: 90%;
        height: 180px;
    }

    .footer__bot_wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 5%;
        padding: 3% 0;
    }

    .footer__bot_list-wrapper {
        display: flex;
        flex-direction: row;
        width: 100%;
        justify-content: center;
        margin-top: 5%;
    }
}

@media (max-width: 425px) {
    /*Header*/
    .header__nav > ul {
        flex-direction: column;
    }

    .header__nav li {
        padding-bottom: 2px;
    }

    .header__nav a {
        line-height: 2;
    }

    .sectionPromo__banner {
        height: 110px;
    }

    .sectionPromo__banner_title {
        display: none;
    }

    .sectionPromo__banner_content {
        display: none;
    }

    .sectionPromo__banner_btn {
        width: 40%;
        font-size: 16px;
    }

    /*Home page*/
    .sectionProductType > .container {
        flex-direction: column;
        align-items: center;
    }

    .sectionProductType__item {
        width: 80%;
        margin-bottom: 10%;
    }

    .sectionProductType__item > p {
        width: 100%;
    }

    .sectionProductType__btn {
        width: 80%;
    }

    .sectionSelection__item {
        width: calc(49% - 20px);
    }

    .sectionAdvantages > .container {
        flex-direction: column;
        margin-top: 15%;
    }

    .sectionAdvantages__item {
        width: 100%;
        margin-bottom: 10%;
    }

    .sectionUsDifferent {
        height: 300px;
    }

    .sectionUsDifferent__title {
        font-size: 20px;
    }

    .sectionUsDifferent__content {
        display: none;
    }

    .sectionUsDifferent__btn {
        width: 70%;
        font-size: 16px;
    }

    /*Shop page*/
    .product__item {
        width: 100%;
    }

    /*Product page*/
    .product__wrapper {
        flex-direction: column;
        align-items:center;
        width: 85%;
        gap: 15px;
    }

    .product__prices {
        width: 100%;
    }

    /*Cart page*/
    .productsCart__item_img {
        display: none;
    }

    .productsCart__item_quantity {
        max-width: 70px;
    }

    .productsCart__item_quantity > input {
        width: calc(100% - 20px);
        font-size: 14px;
        padding: 5px 10px;
    }

    /*Subscriptions page*/
    .subscriptions__sectionPromoBanner {
        height: 110px;
    }

    .subscriptions__sectionPromoBanner_wrapper {
        width: 80%;
    }

    .subscriptions__sectionPromoBanner_content {
        display: none;
    }

    .subscriptions__sectionPromoBanner_btn {
        width: 70%;
        font-size: 16px;
    }

    .subscriptions__sectionPromoBanner_wrapper:after {
        margin: -30px -200px;
    }

    .subscriptions__sectionAdvantages_title {
        font-size: 20px;
    }

    .subscriptions__sectionAdvantages_wrapper {
        flex-direction: column;
        align-items: center;
    }

    .subscriptions__sectionAdvantages_item {
        width: 100%;
    }

    .subscriptions__sectionSelection {
        margin-top: 15%;
    }

    .subscriptions__sectionSelection_items {
        flex-direction: column;
        align-items: center;
        width: 80%;
    }

    .subscriptions__sectionGiveSubscription {
        height: 110px;
    }

    .subscriptions__sectionSelection_item {
        width: 100%;
    }

    .subscriptions__sectionGiveSubscription_content {
        display: none;
    }

    .subscriptions__sectionGiveSubscription_btn {
        width: 30%;
        font-size: 16px;
        margin-top: 0;
    }

    /*Footer*/
    .footer__top .container {
        height: 100px;
    }

    .footer__bot_list > ul {
        margin-bottom: 15%;
    }
}

@media (max-width: 425px) {
    /*Header*/
    .sectionPromo__banner_btn {
        width: 60%;
    }

    /*Home page*/
    .sectionSelection__items {
        justify-content: center;
    }

    .sectionSelection__item {
        width: 80%;
    }

    /*Product page*/
    .product__wrapper {
        width: 100%;
    }

    .prices__bagPrice_bagSize {
        width: calc(100% + 30px);
    }

    .product__prices_bagPrice {
        padding: 0 15px;
    }

    /*Subscriptions page*/
    .subscriptions__sectionPromoBanner_btn {
        width: 80%;
    }

    .subscriptions__sectionGiveSubscription_wrapper {
        width: 80%;
        margin-left: 10%;
    }

    .subscriptions__sectionGiveSubscription_btn {
        width: 40%;
    }

    /*Footer*/
    .footer__top .container {
        height: 95px;
    }

}