* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #FFFFFF;
    color: #000000;
    /*font-family: Arial, Helvetica, sans-serif;*/
    font-size: 16px;
    font-family: var(--typeBasePrimary), var(--typeBaseFallback);
}

.container {
    width: 80%;
    max-width: 1420px;
    margin: 0 auto;
}

/*Header*/
.header {
    box-shadow: 0 0 25px rgb(0 0 0/10%);
    position: sticky;
    top: 0;
    z-index: 6;
    background-color: #FFFFFF;
}

.header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__nav > ul {
    display: flex;
    align-items: center;
    list-style: none;
}

.header__nav li {
    padding-bottom: 10px;
}

.header__nav li:nth-child(1) a {
    padding: 16px 20px 10px 0;
    line-height: 0;
}

.header__nav a {
    display: inline-block;
    position: relative;

    padding: 0 20px;

    line-height: 3;

    font-weight: 700;
    font-size: 16px;
    text-decoration: none;
    color: black;
    font-family: var(--typeHeaderPrimary), var(--typeHeaderFallback);
    letter-spacing: var(--typeHeaderSpacing);
}

.header__nav li:not(:first-child) a:after {
    display: block;
    position: absolute;

    left: auto;
    right: auto;

    width: 0;
    height: 2px;
    background-color: #000000;
    content: "";
}

.header__nav li:not(:first-child) a:hover:after,
.header__nav li:not(:first-child) a:focus:after {
    width: calc(100% - 40px);
}

.header__nav_sub {
    display: none;
}

.header__cart {
    display: flex;
    align-items: center;
    gap: 10px;
}

.header__cart > a {
    color: #000000;
}

.header__cart svg {
    width: 35px;
}

.header__cart_count {
    background-color: #ba4444;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    color: #FFFFFF;

    line-height: 18px;
    text-align: center;
    font-size: 14px;

    position: relative;
    right: -50px;
    top: 5px;
}

/*Shop*/
.productsCatalog__wrapper {
    display: flex;
    gap: 1.5%;
}

.accordion {
    width: 15%;
    display: flex;
    flex-direction: column;
}

.accordion__item {
    maxHeight: 25px;
    padding: 17px 10px;
    border-bottom: 1px solid #c9c9c9;
    overflow: hidden;
    cursor: pointer;
}

.accordion__item input {
    margin-right: 5px;
}

.accordion__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.accordion__header h4 {
    transition: 0.2s ease-in-out;
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: bold;
}

.accordion__header i {
    transition: 0.2s ease-in-out;
    transform-origin: center;
    margin-bottom: 10px;
}

.accordion__header:hover i {
    color: #10d6f5;
}

.accordion__subMenu {
    margin: 5px;
    list-style: none;
}

.accordion__subMenu > li:hover {
    text-decoration: underline;
}

.page {
    margin-top: 2%;
}

.productsCatalog {
    display: flex;
    flex-direction: column;
    width: 85%;
}

.productsCatalog__items {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    gap: 1%;
}

.product__item {
    width: calc(24% - 20px);

    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 30px 10px 40px 10px;
    border-radius: 10px;

    margin-top: 1%;
    background: rgba(246, 246, 246, .9);

    color: #000000;
    text-decoration: none;
    cursor: pointer;
}

.product__item > img {
    width: 80%;
}

.product__item_description {
    display: flex;
    flex-direction: column;
    justify-content: left;

    width: 90%;

    margin-top: 20px;
    padding-left: 25px;
    font-size: 18px;
}

.product__item_description-name {
    font-weight: 700;
    margin-bottom: 10px;
}

.productsCatalog__btns {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 65px;
}

.productsCatalog__btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    background-color: #a03332;
    color: white;
    cursor: pointer;
}

.productsCatalog__btns_pages {
    display: flex;
    list-style: none;
}

.productsCatalog__btns_pages > li {
    cursor: pointer;
    font-size: 18px;
    padding: 0 18px;
}

.productsCatalog__btns_pages > li:hover {
    text-decoration: underline;
}

.productsCatalog__btns_pages .active {
    color: #b62525;
}

/*Product page*/
.product__wrapper {
    display: flex;
    width: 100%;
    gap: 5%;
}

.product__image {
    border: 1px solid #cdc9c9;
}

.product__image img {
    width: 100%;
}

.product__prices {
    display: flex;
    width: 70%;
    flex-direction: column;
}

.product__prices_title {
    font-weight: 700;
    font-size: 18px;
    margin: 40px 0 10px 0;
}

.product__prices_select {
    display: flex;
    gap: 1%;
}

.product__prices_bagPrice {
    display: flex;
    flex-direction: column;
    align-items: center;

    border: 2px solid #cdc9c9;
    cursor: pointer;

    font-size: 18px;
    padding: 0 25px;
}

.product__prices_bagPrice.active {
    border-color: #000000;
}

.prices__bagPrice_bagSize {
    display: block;
    width: calc(100% + 50px);
    text-align: center;

    font-size: 16px;
    font-weight: 700;
    background-color: #f3f3f3;

    padding: 7px 0;
}

.prices__bagPrice_Price {
    padding: 10px 0;
}

.prices__bagPrice_PriceLb {
    font-size: 14px;
    padding: 10px 0;
}

.product__prices_totalPrice {
    font-size: 20px;
}

.product__prices_totalQuantity {
    display: inline-block;
    position: relative;
    overflow: visible;

    border: 1px solid #cdc9c9;
    max-width: 100px;
    min-width: 60px;
}

.product__prices_totalQuantity > input {
    display: block;
    width: calc(100% - 40px);
    border: none;
    outline: none;
    text-align: center;

    font-size: 16px;
    padding: 5px 20px;
}

.product__prices_totalQuantity > button {
    position: absolute;
    display: block;
    overflow: hidden;

    top: 0;
    bottom: 0;

    background-color: #FFFFFF;
    border: none;
    cursor: pointer;

    font-size: 18px;
    color: #7b7a7a;

    padding: 0 10px;
}

.product__prices_totalQuantity > button:hover {
    background-color: #e2e1e1;
}

.prices__totalQuantity_btn-left {
    left: 0;
}

.prices__totalQuantity_btn-right {
    right: 0;
}

.product__prices_addToCart {
    background-color: #a03332;
    border: none;
    color: #FFFFFF;
    font-weight: 800;
    font-size: 16px;
    cursor: pointer;

    width: 100%;
    margin-top: 30px;
    padding: 15px 0;
}

.product__description {
    display: flex;
    flex-direction: column;

    margin-top: 5%;
}

.product__description_title {
    padding-left: 2%
}

.product__description_content {
    display: flex;
    flex-direction: column;
    border: 1px solid #cdc9c9;

    margin-top: 1%;
    padding: 2% 2% 5% 2%;
}

.product__description_content > p {
    line-height: 1.5;
}

.product__description_characteristic {
    list-style: none;
    display: flex;
    flex-direction: column;


    font-size: 20px;

    gap: 25px;
    margin-top: 3%;
}

.product__description_characteristic span {
    font-weight: 700;
}


/*Cart page*/
.productsCart__items {
    display: flex;
    flex-direction: column;
    list-style: none;
    gap: 10px;

    border-bottom: 1px solid #cdc9c9;
    padding-bottom: 10px;
}

.productsCart__item {
    display: flex;
    align-items: center;
}

.productsCart__item_wrapper {
    display: flex;
    gap: 5%;
    width: 40%;

    text-decoration: none;
    color: #000000;
}

.productsCart__item_img {
    border: 1px solid #cdc9c9;
    border-radius: 10px;
    width: 20%;
    height: max-content;
    overflow: hidden;
}

.productsCart__item_img > img {
    width: 100%;
}

.productsCart__item_description {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
}

.productsCart__item_description-name {
    font-weight: 700;
}

.productsCart__item_quantity {
    display: inline-block;
    position: relative;
    overflow: visible;

    border: 1px solid #cdc9c9;
    max-width: 100px;
    min-width: 60px;

    height: 80%;
}

.productsCart__item_quantity > input {
    display: block;
    width: calc(100% - 40px);
    border: none;
    outline: none;
    text-align: center;

    font-size: 16px;
    padding: 5px 20px;
}

.productsCart__item_quantity > button {
    position: absolute;
    display: block;
    overflow: hidden;

    top: 0;
    bottom: 0;

    background-color: #FFFFFF;
    border: none;
    cursor: pointer;

    font-size: 18px;
    color: #7b7a7a;

    padding: 0 10px;
}

.productsCart__item_quantity > button:hover {
    background-color: #e2e1e1;
}

.item__quantity_btn-left {
    left: 0;
}

.item__quantity_btn-right {
    right: 0;
}

.productsCart__item_price {
    font-size: 18px;
    margin-left: 10%;
}

.item__quantity_btn-delete {
    background-color: #FFFFFF;
    border: none;
    margin-left: auto;
    cursor: pointer;
}

.item__quantity_btn-delete:hover {
    color: #a03332;
}

.item__quantity_btn-delete > svg {
    width: 30px;
}

.productsCart__totalPrice {
    margin-top: 2%;
    font-size: 18px;
    font-weight: 700;
}


/*home page*/
.sectionPromo__banner {
    margin-top: -2%;
    height: 505px;
    background-size: cover;
    background-image: url(../images/main-feature-wide-november-bundle-2022-2000x505_1944x.jpg);
    background-repeat: no-repeat;

}

.sectionPromo__banner .container {
    padding-top: 5%;
}

.sectionPromo__banner-wrapper {
    color: #FFFFFF;
    width: 45%;
}

.sectionPromo__banner_content {
    font-size: 18px;
    font-weight: 500;
    margin-top: 2%;
}

.sectionPromo__banner_btn {
    display: block;
    font-weight: 700;
    font-size: 18px;

    color: #000000;
    background-color: #FFFFFF;

    text-align: center;
    width: 20%;
    margin-top: 3%;
    padding: 10px 0;
}

.sectionPromo__banner_btn:hover {
    background-color: #a03332;
    color: #FFFFFF;
}

