}

.sectionSelection__header_btn {
    display: block;
    border: 1px solid #000000;
    font-weight: 700;
    font-size: 16px;

    color: #000000;
    background-color: #FFFFFF;
    text-align: center;
    padding: 7px 10px;
}

.sectionSelection__header_btn:hover {
