    background-color: #000000;
}


.sectionSelection__items {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    gap: 1%;
}

.sectionSelection__item {
    width: calc(19% - 20px);

    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 30px 10px 40px 10px;
    border-radius: 10px;

    margin-top: 1%;
    background: rgba(246, 246, 246, .9);

    color: #000000;
    text-decoration: none;
    cursor: pointer;
}

.sectionSelection__item > img {
    width: 80%;
}

.sectionSelection__item_description {
    display: flex;
    flex-direction: column;
    width: 90%;

    margin-top: 20px;
    font-size: 18px;
}

.sectionSelection__item_description-name {
    font-weight: 700;
    margin-bottom: 10px;
}

.sectionProductType {
    margin-top: 2%;
}

.sectionProductType__item img {
    width: 100%;
}

.sectionProductType__item_title {
    font-size: 22px;
    font-weight: 700;
    text-align: center;
}

.sectionProductType__item > p {
    width: 70%;
}

.sectionProductType__btn {
    width: 50%;
}

.sectionAdvantages > .container, .sectionProductType > .container {
    display: flex;
    gap: 5%;
}

.sectionAdvantages__item, .sectionProductType__item {
    display: flex;
    flex-direction: column;
    width: 33%;
    align-items: center;
    gap: 10px;
}

.sectionAdvantages__item > p, .sectionProductType__item > p {
    text-align: center;
    font-size: 16px;
    line-height: 1.5;
}

.sectionAdvantages {
    margin-top: 5%;
}

.sectionAdvantages__item_title {
    font-size: 20px;
    font-weight: 700;
    text-align: center;
}

.sectionUsDifferent {
    display: flex;
    align-items: center;

    margin-top: 5%;
    height: 505px;
    background-size: cover;
    background-image: url(../images/main-feature-wide-real-customers-about-us-background_2400x.jpg);
    background-repeat: no-repeat;
}

.sectionUsDifferent__wrapper {
    display: flex;
    flex-direction: column;
    width: 17%;
    margin-left: auto;
    background-color: #FFFFFF;
    padding: 1.5%;
    gap: 20px;
    margin-right: 3%;
}

.sectionUsDifferent__title {
    font-weight: 500;
}

.sectionUsDifferent__content {
    font-size: 18px;
}

.sectionUsDifferent__btn, .sectionProductType__btn {
    display: block;
    font-weight: 700;
    font-size: 18px;

    background-color: #a03332;
    color: #FFFFFF;

    text-align: center;

    margin-top: 3%;
    padding: 10px 0;
}

.sectionUsDifferent__btn {
    width: 40%;
}

.sectionUsDifferent__btn:hover, .sectionProductType__btn:hover {
    background-color: #802b2b;
    color: #FFFFFF;
}


/*Subscription page*/
.subscriptions__sectionPromoBanner {
    margin-top: -2%;
    height: 505px;
    background-size: cover;
    background-image: url(../images/subscription-landing-page-main-feature-image-2000x505_1944x.jpg);
    background-repeat: no-repeat;
}

.subscriptions__sectionPromoBanner .container {
    padding-top: 5%;
}

.subscriptions__sectionPromoBanner_wrapper {
    color: #000000;
    width: 45%;
    opacity: 1;
    transition: all .4s ease .2s;

    position: relative;
    z-index: 4;
}

.subscriptions__sectionPromoBanner_wrapper:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    background: radial-gradient(rgba(0, 0, 0, .3) 0%, transparent 60%);
    margin: -100px -200px;
}

.subscriptions__sectionPromoBanner_title {
    font-size: 46px;
    text-shadow: 0 3px 7px rgb(0 0 0 / 15%);
}

.subscriptions__sectionPromoBanner_content {
    font-size: 20px;
    font-weight: 500;
    margin-top: 2%;

    text-shadow: 0 3px 7px rgb(0 0 0 / 15%);
}

.subscriptions__sectionPromoBanner_btn {
    display: block;
    font-weight: 700;
    font-size: 18px;

    color: #000000;
    background-color: #FFFFFF;

    text-align: center;
    width: 35%;
    margin-top: 3%;
    padding: 10px 0;
}

.subscriptions__sectionPromoBanner_btn:hover {
    background-color: #a03332;
    color: #FFFFFF;
}

.subscriptions__sectionAdvantages {
    margin-top: 2%;
}

.subscriptions__sectionAdvantages > .container {
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 30px;
}

.subscriptions__sectionAdvantages_title {
    font-weight: 500;
}

.subscriptions__sectionAdvantages_wrapper {
    display: flex;
    gap: 5%;
}

.subscriptions__sectionAdvantages_item {
    display: flex;
    flex-direction: column;
    width: 33%;
    align-items: center;
    gap: 10px;
}

.subscriptions__sectionAdvantages_item > img {
    height: 105px;
}

.subscriptions__sectionAdvantages_item-title {
    font-size: 24px;
    font-weight: 700;
}

.subscriptions__sectionAdvantages_item > p {
    text-align: center;
    font-size: 16px;
    line-height: 1.5;
}

.subscriptions__sectionSelection {
    margin-top: 5%;
}

.subscriptions__sectionSelection .container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.subscriptions__sectionSelection_header {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.subscriptions__sectionSelection__header-title {
    font-weight: 500;
    margin-bottom: 10px;
}

.subscriptions__sectionSelection_items {
    display: flex;
    justify-content: center;

    width: 60%;
    gap: 1%;

