}

.subscriptions__sectionSelection_item {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 33.3%;

    padding: 30px 10px 40px 10px;
    border-radius: 10px;

    margin-top: 1%;
    background: rgba(246, 246, 246, .9);

    color: #000000;
    text-decoration: none;
    cursor: pointer;
}

.subscriptions__sectionSelection_item > img {
    width: 80%;
}

.subscriptions__sectionSelection_item-description {
    display: flex;
    flex-direction: column;
    width: 90%;

    margin-top: 20px;
    font-size: 18px;
}

.subscriptions__sectionSelection_item-description-name {
    font-weight: 700;
    margin-bottom: 10px;
}

.subscriptions__sectionGiveSubscription {
    display: flex;
    align-items: center;

    margin-top: 5%;
    height: 505px;
    background-size: cover;
    background-image: url(../images/subscription-landing-page-gift-callout-image-1920x505_2400x.jpg);
    background-repeat: no-repeat;
}

.subscriptions__sectionGiveSubscription_wrapper {
    display: flex;
    flex-direction: column;
    width: 17%;

    background-color: #FFFFFF;
    padding: 1.5%;
    gap: 20px;
    margin-left: 3%;
    margin-bottom: 3%;
}

.subscriptions__sectionGiveSubscription_title {
    font-weight: 500;
}

.subscriptions__sectionGiveSubscription_content {
    font-size: 18px;
}

.subscriptions__sectionGiveSubscription_btn {
    display: block;
    font-weight: 700;
    font-size: 18px;

    background-color: #a03332;
    color: #FFFFFF;

    text-align: center;

    margin-top: 3%;
    padding: 10px 0;
}

.subscriptions__sectionGiveSubscription_btn {
    width: 40%;
}

.subscriptions__sectionGiveSubscription_btn:hover {
    background-color: #802b2b;
    color: #FFFFFF;
}


/*Footer*/
.footer__top .container {
    margin-top: 5%;
    margin-bottom: 5%;
    width: 70%;
    height: 350px;
    background-image: url(../images/Footer_image_1600x.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.footer__top {
    border-bottom: 1px solid #cdc9c9;
}

.footer__bot_wrapper {
    display: flex;
    justify-content: center;
    gap: 5%;

    padding: 3% 0;
}

.footer__bot_title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
}

.footer__bot_list > ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 10px;
}


.footer__bot_list li, a {
    text-decoration: none;
    color: #504e4e;
    font-size: 14px;
}

.footer__bot_list li > span {
    font-weight: 700;
}

.footer__bot_list.contacts a {
    display: flex;
    align-items: center;
}

.footer__bot_list.contacts img {
    margin-right: 5px;
}

.footer__bot_list.payCard ul {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    gap: 10%;
}

.footer__bot_list.payCard li {
    width: 25%;
    margin-bottom: 2%;
}

.footer__bot_list.payCard li:not(:last-child) img {
    width: max-content;
    border: 1px solid #cdc9c9;
    border-radius: 10%;
    padding: 0 5px;
    text-align: center;
}

.footer__bot_list.payCard li:not(:last-child) img {
    width: 30px;
}

.footer__bot_list.payCard li:last-child img {
    height: 30px;
    width: 40px;
}

.footer__bot_list-wrapper {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 10%;
}

.footer__bot_list.social > ul {
    flex-direction: row;
}

.footer__bot_copyrights {
    border-top: 1px solid #cdc9c9;
    text-align: center;
    padding: 2% 0;
    font-size: 14px;
    color: #454444;
}


:root {
    --typeHeaderPrimary: Futura;
    --typeHeaderFallback: sans-serif;
    --typeHeaderSize: 32px;
    --typeHeaderWeight: 500;
    --typeHeaderLineHeight: 1.1;
    --typeHeaderSpacing: 0.025em;
    --typeBasePrimary: -apple-system;
    --typeBaseFallback: BlinkMacSystemFont, Segoe UI, Roboto, Ubuntu, Helvetica Neue, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
    --typeBaseSize: 15px;
    --typeBaseWeight: 400;
    --typeBaseSpacing: 0.0em;
    --typeBaseLineHeight: 1.6;
    --colorSmallImageBg: #ffffff;
    --colorSmallImageBgDark: #f7f7f7;
    --colorLargeImageBg: #0f0f0f;
    --colorLargeImageBgLight: #303030;
    --iconWeight: 4px;
    --iconLinecaps: round;
    --buttonRadius: 0px;
    --btnPadding: 11px 20px;
    --roundness: 0px;
    --gridThickness: 0px;
    --productTileMargin: 10%;
    --collectionTileMargin: 5%;
    --swatchSize: 40px;
}

